import InputCell from '@/components/InputCell/InputCell.vue'
import UploadItem from '@/components/UploadItem/UploadItem.vue'
import UserModel from '@/models/user.js'
import StoreModel from '@/models/store.js'
import { config } from '../../../config.js'
const userModel = new UserModel()
const storeModel = new StoreModel()
export default {
	data() {
		return {
			isEdit: true, // 是否编辑状态
			resume: {
				id:　'',
				name: '', // 公司名称
				cityCode: '', // 城市码
				address: '', // 地址
				industryCode: '', // 所在行业
				contactName: '', // 招聘联系人
				businessImg: '', // 营业执照
				companyImg: '', // 工作场景
				logo: '', // 公司logo
				phone: '', // 电话号码
			},
			businessImgChanged: false,
			companyImgChanged: false,
			logoChanged: false,
			imageChanged: false,
			pictureIndex: 0,
			verifyText: '发送验证码',
			verifyCode: '',
			cityCodeText: '', // 区域文字
			citySelected: [], // 已选中的城市
			readySendCode: true,
			surplusSecond: 0,
			verifyCodeTimer: null
		}
	},
	components: {
		InputCell,
		UploadItem
	},
	onLoad(options){
		if(options.isEdit === 'false'){
			this.isEdit = false
		}
		if(options.isEdit === 'true'){
			this.isEdit = true
		}
	},
	methods:{
		chooseImage(){
			// 让用户选择相册或者拍照
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.avatar = res.tempFilePaths[0]
				}
			})
		},
		cityPickerChange(e){
			const { code, value } = e.detail
			this.resume.cityCode = code.join(',')
			this.cityCodeText = Array.from( new Set(value)).join('')
		},
		chooseBusinessImg(){
			const that = this
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					that.resume.businessImg = res.tempFilePaths[0]
					that.getImageWebUrl(res.tempFilePaths[0], 'businessImg')
				}
			})
		},
		chooseLogo(){
			const that = this
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					that.resume.logo = res.tempFilePaths[0]
					that.getImageWebUrl(res.tempFilePaths[0], 'logo')
				}
			})
		},
		chooseCompanyImg(){
			const that = this
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					that.resume.companyImg = res.tempFilePaths[0]
					that.getImageWebUrl(res.tempFilePaths[0], 'companyImg')
				}
			})
		},
		sendVerifyCode(){
			if(!this.readySendCode){
				return 
			}
			if(!this.resume.phone){
				uni.showToast({
					icon:'none',
					title:'请输入电话号码'
				})
				return
			}
			userModel.getVerifyCode(this.resume.phone, 'B').then(res=>{
				// 请求成功,并判断code是否正确
				const { code, message, data } = res.data 
				if(code === '0'){
					uni.showToast({
						title: '验证码已发送，请注意查收'
					})
					this.readySendCode = false
					this.surplusSecond = 60
					// this.focusIndex = 3
					this.verifyCodeTimer = setInterval(()=>{
						if(this.surplusSecond > 0){
							this.surplusSecond -= 1
						}else{
							this.surplusSecond = 0
							this.readySendCode = true
							clearInterval(this.verifyCodeTimer)
						}
					}, 1000)
					// this.focusIndex = 3	
				}else{
					uni.showToast({
						icon:'none',
						title: message
					})
				}
				
			}).catch(err=>{
				uni.showToast({
					icon:'none',
					title:'获取验证码失败'
				})
			})
		},
		postVerifyCode(){
			const that = this
			userModel.login(this.resume.phone, this.verifyCode, 'B').then(res=>{
				const { code, message, data } = res.data
				if(code === '0'){
					uni.setStorageSync('token',data.token)
					that.saveStore()
				}else {
					uni.showToast({
						icon:'none',
						title: message
					})
				}
			})
		},
		saveStore(){
			const that = this
			const array = Object.values(this.resume)
			storeModel.saveStore(...array).then(res=>{
				const { code, message, data } = res.data
				if(code === '0'){
					if(!this.resume.id){
						uni.showToast({
							title: '注册成功'
						})
						uni.setStorageSync('token', data.token)
						that.hasToken = true
						setTimeout(()=>{
							uni.reLaunch({
								url: '../../seller/main/main'
							})	
						},2000)	
					}else{
						uni.showToast({
							title: '修改成功'
						})
						setTimeout(()=>{
							uni.navigateBack({
								delta: 1
							})	
						},2000)
					}
				}else{
					// 错误处理
					uni.showToast({
						icon:'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon:'none',
					title: '提交失败，请稍候再试'
				})
			})
			
		},
		nameChanged(value){
			this.resume.name = value
		},
		cityChanged(value){
			this.resume.cityCode = value
		},
		addressChanged(value){
			this.resume.address = value
		},
		industryChanged(value){
			this.resume.industryCode = value
		},
		contactNameChanged(value){
			this.resume.contactName = value
		},
		phoneChanged(value){
			this.resume.phone = value
		},	
		verifyCodeChanged(value){
			this.verifyCode = value
		},
		getImageWebUrl(path, name){
			const that = this
			uni.showLoading({
				title: '上传中，请稍候...'
			})
			uni.uploadFile({
				url: config.base_url + '/store/getImageWebUrl',
				filePath: path,
				name: 'file',
				success(res){
					// 这里返回图片的url地址 response.data.url
					uni.hideLoading()
					let response = res.data
					if(typeof response === 'string'){
						response = JSON.parse(response)
					}
					const { code, message, data } = response
					if(code === '0'){
						uni.showToast({
							title: '上传成功'
						})
						that.imageChanged = true
						that.resume[name] = data.url
					}else{
						uni.showToast({
							icon:'none',
							title: message
						})
					}
					
				},
				fail: res=>{
					uni.showToast({
						icon:'none',
						title: '上传失败，请稍候再试'
					})
				}
			})
		}
	},	
}
